import { keyframes, styled } from '@stitches/react';

const rotateClockwise = keyframes({
  '0%': {
    transform: 'rotate(0deg)',
  },
  '100%': {
    transform: 'rotate(360deg)',
  },
});

const rotateCounterClockwise = keyframes({
  '0%': {
    transform: 'rotate(0deg)',
  },
  '100%': {
    transform: 'rotate(-360deg)',
  },
});

const DoubleAlternateSpinner = styled('div', {
  'position': 'relative',

  '& > div:first-child': {
    position: 'absolute',
    height: '40px',
    width: '40px',
    borderRadius: '9999px',
    border: '1px solid gray',
    borderTopColor: 'transparent',

    animation: `${rotateClockwise} 1s infinite`,
  },

  '& > div:last-child': {
    position: 'absolute',
    margin: '5px',
    height: '30px',
    width: '30px',
    borderRadius: '9999px',
    border: '1px solid gray',
    borderBottomColor: 'transparent',

    animation: `${rotateCounterClockwise} 1s infinite`,
  },
});

const pulse = keyframes({
  '0%': {
    transform: 'scale(1)',
  },
  '100%': {
    transform: 'scale(1.3)',
  },
});

const PulsingDots = styled('div', {
  'display': 'flex',
  'flexDirection': 'row',
  'justifyItems': 'space-evenly',

  '& > div': {
    height: '12px',
    width: '12px',
    background: 'gray',
    borderRadius: '9999px',
  },

  '& > div:not(:last-child)': {
    marginRight: '8px',
  },

  '& > div:first-child': {
    animation: `${pulse} 0.7s infinite`,
    animationDelay: '0s',
  },

  '& > div:nth-child(2)': {
    animation: `${pulse} 0.7s infinite`,
    animationDelay: '0.5s',
  },

  '& > div:last-child': {
    animation: `${pulse} 0.7s infinite`,
    animationDelay: '1s',
  },
});

export const Spinners = () => {
  return (
    <div className="flex w-full h-full flex-row justify-evenly items-center">
      <DoubleAlternateSpinner>
        <div />
        <div />
      </DoubleAlternateSpinner>

      <PulsingDots>
        <div />
        <div />
        <div />
      </PulsingDots>
    </div>
  );
};
